.expertise {
  grid-column: span 2;
  background-color: #fff;
  border-radius: 30px;
  padding: 72px 20px;
  @include pageGrid;


  @include onTablet {
    grid-column: span 6;
    padding: 128px 40px;
  }

  &__title {
    color: $main-title-color;
    text-align: center;
    font-family: "Poppins";
    font-size: 44px;
    font-weight: 600;
    line-height: 52.8px;
    grid-column: span 2;

    @include onTablet {
      grid-column: span 6;
      font-size: 52px;
      line-height: 78px;
    }
  }

  &__text {
    color: $main-text-color;
    text-align: center;
    font-family: "Open Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
  }

  &__title--h3 {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 16px;
  }

  &__image {
    margin-top: 56px;
    margin-bottom: 32px;
    width: 184px;
    border-radius: 30px;

    @include onTablet {
      margin-top: 64px;
    }
  }

  &__branding {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }
  }
  &__communication {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }
  }
  &__strategy {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }
  }

}
