.header {
  background-color: $header-background;
  height: 100vh;
  padding: 20px 20px;
  box-sizing: border-box;
  position: relative;


  @include onTablet {
    padding: 20px 40px;
  }

  &__main {
    @include pageGrid;
  }

  &__main--title {
    grid-column: span 2;
    margin-top: 48px;
    color: #FFF;
    font-family: "Poppins";
    font-size: 52px;
    font-style: normal;
    font-weight: 600;
    line-height: 62.4px;
    letter-spacing: -2px;


    @include onTablet {
      grid-column: span 6;
      font-size: 64px;
      line-height: 68px;
    }
  }

  &__main--text {
    grid-column: span 2;
    color: #FFF;
    font-family: "Open Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    margin-top: 32px;

    @include onTablet {
      grid-column: span 4;
    }
  }

  &__bottom {
    position: absolute;
    bottom: 0px;
    right: 0;
    left: 0;
    background-color: #eee;
    overflow: hidden;
  }

  &__button {
    height: 50px;
    border-radius: 8px;
    background: #2060F6;
    border: none;
    width: 100%;
    transition-duration: 0.5s;
    margin-top: 72px;
    grid-column: span 2;

    @include onTablet {
      grid-column: span 3;
    }
  }

  &__button:hover {
    transform: scale(1.01);
  }


  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }

  &__bottom--bg {
    background-image: linear-gradient(#2c2c2c, #2c2c2c, $c-gray, $c-gray);
  }

  &__bottom__photo-slide {
    // max-height: 430px;
    // min-height: 180px;
    height: 390px;
    background-image: url(../images/slider/slide-img-2.png);
    border-radius: 30px;

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    bottom: 0;
  }
}

.container {
  grid-column: span 2;
}
