.main__slider {
  bottom: 24px;
  position: absolute;
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 40px;
  }

  &__botton {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    border: none;
  }

  &__botton--1 {
    display: block;
    height: 18px;
    width: 18px;
    background-image: url(../icons/mdi_chevron_left.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__botton--2 {
    display: block;
    height: 18px;
    width: 18px;
    background-image: url(../icons/mdi_chevron_right.svg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__bottons {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
  }
}

.description {
  display: flex;
  flex-direction: column;
  gap: 8px;


  &__intro {
    color: #FFF;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__text {
    width: 280px;
    color: #FFF;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
  }
}
