.services {
  grid-column: span 2;
  text-align: left;
  padding-inline: 20px;
  @include pageGrid;

  @include onTablet {
    grid-column: span 6;
  }

  &__description {
    grid-column: span 2;
    color: #c0Cdd7;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 16px;
  }

  &__title {
    grid-column: span 2;
    color: $main-title-color;
    font-family: "Poppins";
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    margin-bottom: 24px;
  }

  &__text {
    grid-column: span 2;
    color: $main-text-color;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
  }
}
