.nav-top {
  display: flex;
  justify-content: space-between;

  &__logo {
    display: flex;
  }
  &__icons {
    display: flex;
    align-items: center;
    gap: 32px;
  }

  &__button {
    display: flex;
    background-color: #2c2c2c;
    border: none;
    padding: 0;

    padding-bottom: 8px;
    border-bottom: 2px solid #2060F6;
  }

  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
  }
}
